<template>
    <section class="consultor">
        <v-header>购车咨询</v-header>
        <scroll  :data="consultorArr" @scroll="scroll" :listen-scroll="listenScroll" :probe-type="probeType" :pullup="isShow"  :pulldown="isShow" @pulldown="pulldown"  @scrollToEnd="pullup"  class="consultor-list" ref="consultorList">
            <ul class="consultor-list-content" v-if="consultorArr.length">
                <li class="advertise">
                    <img :src="bannerUrl" alt="">
                </li>
                <li class="consultor-item" v-for="(item,index) in consultorArr" :key="index">
                    <div class="consultor-content">
                        <div class="consultor-img">
                            <img :src="item.icon" alt="" width="70" height="70" class="img">
                            <img :src="majorimgUrl" alt="" class="major" width="100%">
                        </div>
                        <article class="consultor-desc">
                            <header class="consultor-title"><h4>{{item.name}}</h4><span>{{`擅长：${item.skill}`}}</span></header>
                            <p class="desc-content">{{item.introduce}}</p>
                        </article>
                    </div>
                    <div class="consultor-btn">
                        <div class="cotact-now">
                            <a href="">
                                <i class="icon-phone"></i>
                                马上联系
                            </a>
                        </div>
                        <div class="question">
                            <a @click="getContactor(item)">
                                <i class="icon-questions"></i>
                                向TA提问
                            </a>                            
                        </div>
                    </div>
                </li>
            </ul>
        </scroll>
        <div class="contact-modal" v-if="showForm">
             <v-contactor :contactor = 'contactor' :majorimgUrl="majorimgUrl" @close = "close"></v-contactor>
        </div>       
    </section>
</template>

<script>
import vHeader from '@/components/mobile/m-header'
import vContactor from '@/components/mobile/m-contactor'
import Scroll from '@/components/base/scroll/scroll'
import Loading from '@/components/base/loading/loading'
import {MOBILE_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data(){
        return{
            consultorArr:[],
            scrollY: 0,
            isShow: true, 
            contactor:{}, 
            page:1,
            bannerUrl:'',
            majorimgUrl:'',
            showForm:false
        }
    },
    created() {
      this.probeType = 3
      this.listenScroll = true
    },
    components:{
        vHeader,
        Scroll,
        Loading,
        vContactor
    },
    methods:{
        close(data){
            this.showForm = data;
        },
        pulldown(){  
            this.consultorArr = [];
            this.page = 1;
            //下拉刷新重新初始化             
            this.$refs.consultorList.$emit('pullrefresh.finishLoad');  
            this.getcarData();
        },  
        pullup(){  
            if(!this.page) return  
            this.page ++   
            //上拉加载重新初始化  
            this.$refs.consultorList.$emit('infinitescroll.reInit');
            this.getcarData(this.page);  
        }, 
        getcarData:function(page){
            let _this = this;
            let data = new Object();
            data.platForm = '020005';
            data.pageSize = '10';
            if(page){
                data.pageNum = page;
            }else{
                data.pageNum = 1;
            }
            $.ajax({
                url:MOBILE_COMMONURL+"/querySaleManageList",
                type:"get",
                data:data,
                dataType:'json',
                asnyc:true,
                success:function(data){
                    console.log(data);
                    if(data.code == SUCCESS_CODE){
                        if(data.data.saleManageList.length > 0){  
                            _this.consultorArr = [..._this.consultorArr, ...data.data.saleManageList]  
                            //单次请求数据加载完毕后  
                            _this.$refs.consultorList.$emit('infinitescroll.finishLoad');  
                        }else{  
                            //把page设置成false 数据已经加载完毕了 不用在加载了  
                            _this.page = false  
                            //所有数据加载完毕后  
                            _this.$refs.consultorList.$emit('infinitescroll.loadedDone');  
                        }
                    }
                }
            })
        },
        getContactor(contactor){
            console.log(contactor);
            this.contactor = contactor;
            this.showForm = true;
        },
        scroll(pos) {
            this.scrollY = pos.y;
            console.log(pos.y);
        }, 
    },
    mounted(){
        this.getcarData();
        this.$refs.consultorList.$el.style.top = `44px`;  
        let deviceRatio = window.devicePixelRatio;
        if(deviceRatio == 1){
            this.bannerUrl = "static/images/banner1.png";
            this.majorimgUrl = "static/images/img_certify.png";
        }else if(deviceRatio == 2){
            this.bannerUrl = 'static/images/banner1@2x.png';
            this.majorimgUrl = "static/images/img_certify@2x.png";
        }else{
            this.bannerUrl= 'static/images/banner1@3x.png';
            this.majorimgUrl = "static/images/img_certify@3x.png";
        }
    }
}
</script>



