<template>
    <section class="contact-form" v-if="Object.keys(contactor).length">
         <div class="close" @click="close"><i class="icon-close"></i></div>
         <div class="consultor-content">
            <div class="consultor-img">
                <img :src="contactor.icon" alt="" width="70" height="70" class="img">
                <img :src="majorimgUrl" alt="" class="major" width="100%">
            </div>
            <article class="consultor-desc">
                <header class="consultor-title"><h4>{{contactor.name}}</h4><span>{{`擅长：${contactor.skill}`}}</span></header>
                <p class="desc-content">{{contactor.introduce}}</p>
            </article>
        </div>
        <div class="contact-form-content">
            <div class="weui-cells weui-cells_form">
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                        <textarea class="weui-textarea" placeholder="请输入您的问题，客服会尽快联系您" rows="3"></textarea>
                    </div>
                </div>
                <div class="weui-cell">
                    <div class="weui-cell__bd">
                        <input class="weui-input" type="number" placeholder="请输手机号" pattern="[0-9]*">
                    </div>
                </div>
                <div class="weui-cell weui-cell_vcode">
                    <div class="weui-cell__bd">
                        <input class="weui-input" type="tel" placeholder="请输入验证码">
                    </div>
                    <div class="weui-cell__ft">
                        <button class="weui-vcode-btn">获取验证码</button>
                    </div>
                </div>
                 <div class="weui-cell">
                    <div class="weui-cell__bd">
                        <input class="weui-input" type="text" placeholder="请输入联系人">
                    </div>
                </div>
                <div class="weui-btn-area">
                    <a class="weui-btn weui-btn_primary" href="javascript:">提交</a>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    props:{
        contactor:{
            type:Object,
            default:{}
        },
         majorimgUrl:{
            type:String,
            default:""
        }
    },
    methods:{
        close(){
           this.$emit("close",false);
        },
        submit(){
            
        }
    }
   
}
</script>



